.orderItem {
    width: 100%;
    background-color: #fff;
    margin-bottom: 10px;
    padding: 0 15px;
    display: flex;
    flex-direction: column;
  }
  
  .orderItem__title {
    display: flex;
    height: 45px;
    align-items: center;
    color: #111;
    font-size: 15px;
    font-weight: bolder;
  }
  
  .orderItem__main {
    display: flex;
    background-color: #fafafa;
  }
  
  .orderItem__imgWrapper {
    height: 80px;
    width: 80px;
    flex-shrink: 0;
    position: relative;
  }
  
  .orderItem__img {
    height: 100%;
    width: 100%;
  }
  
  .orderItem__tag {
    display: inline-block;
    padding: 0 9px;
    height: 22px;
    width: 100%;
    line-height: 22px;
    background-color: #c39c6d;
    color: #fff;
    font-size: 12px;
    position: absolute;
    text-align: center;
  }
  
  .orderItem__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1;
    padding: 0 8px 0 10px;
    min-width: 0;
  }
  
  .orderItem__line {
    font-size: 14px;
    line-height: 28px;
    color: #555;
    white-space: nowrap;
    word-wrap: normal;
    text-overflow: ellipsis;
    overflow: hidden;
    display: inline-block;
  }
  
  .orderItem__bottom {
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .orderItem__type {
    font-size: 13px;
    color: #999;
    line-height: 13px;
  }
  
  .orderItem__btn {
    display: inline-block;
    border: 1px solid #bbb;
    color: #555;
    height: 27px;
    width: 62px;
    border-radius: 2px;
    font-size: 12px;
    text-align: center;
    line-height: 27px;
    margin-left: 13px;
  }
  
  .orderItem__commentContainer {
    width: 100%;
    padding-bottom: 10px; 
  }
  
  .orderItem__comment {
    width: 100%;
    height: 80px;
    border: 1px solid #f1f1f1;
    box-sizing: border-box;
    margin-top: 10px;
  }
  
  .orderItem__starContainer {
    margin-bottom: 5px;
  }
  
  .orderItem__star {
    color: #999;
  }
  
  .orderItem__star--light {
    color: rgb(233, 32, 61);
  }
  
  .orderItem__commentBtn {
    display: inline-block;
    border: 1px solid #bbb;
    color: #555;
    background-color: #fff;
    height: 27px;
    width: 62px;
    border-radius: 2px;
    font-size: 12px;
    text-align: center;
  }
  
  .userMain {
    font-size: 14px;
  }
  
  .userMain__menu {
    height: 46px;
    display: flex;
    background-color: #fff;
    align-items: center;
    color: #111;
    font-size: 14px;
    position: relative;
  }
  
  .userMain__tab {
    flex: 1;
    height: 100%;
    line-height: 46px;
  }
  
  .userMain__title {
    position: absolute;
    top: 0;
    width: 25%;
    text-align: center;
  }
  
  .userMain__title--active {
    border-bottom: 2px solid #f63;
    color: #f63;
  }
  
  .userMain__content {
    list-style: none;
  }
  
  .userMain__emptyIcon {
    margin: 88px auto 0;
    height: 100px;
    width: 100px;
    background-position: center;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-image: url("data:image/png;base64,");
  }
  
  .userMain__emptyText1 {
    font-size: 16px;
    color: #777;
    line-height: 16px;
    text-align: center;
    margin: 16px 0 12px;
  }
  
  .userMain__emptyText2 {
    font-size: 15px;
    color: #999;
    line-height: 15px;
    text-align: center;
  }
  